import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController,
  ModalController
} from "ionic-angular";
import { ColumnMappingViewModel } from "../../Model/ViewModel/ColumnMappingViewModel";

/**
 * Generated class for the ColumnMappingModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page column-mapping-add-edit-modal",
  templateUrl: "column-mapping-add-edit-modal.html"
})
export class ColumnMappingAddEditModalPage {
  item: ColumnMappingViewModel;
  mode: string;
  title: string;
  CanEditTable: boolean;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController
  ) {
    this.item = new ColumnMappingViewModel();
    this.item.schemaname = navParams.data.item.schemaname;
    this.item.tablename = navParams.data.item.tablename;
    this.item.columnname = navParams.data.item.columnname;
    this.item.column_group_key = navParams.data.item.column_group_key;
    this.item.mapping_column = navParams.data.item.mapping_column;
    this.item.mapping_column_desc = navParams.data.item.mapping_column_desc;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;

    this.mode = navParams.data.mode;
    this.CanEditTable = navParams.data.CanEditTable;
    if (this.mode === "POST") this.title = "新增";
    else this.title = "更新";
  }
  SelectColumn() {
    let modal = this.modalCtrl.create("TableDetailSelectModalPage", {
      table_item: this.item,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
       this.item.columnname = select_data.columnname;
    });

    modal.present();
  }
  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  SelectColumnGroup() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.column_group_key,
      code_type:"077",
      code_key:"",
      title:"欄位群組",
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.column_group_key = select_data.code_key;
    });

    modal.present();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad ColumnMappingAddEditModalPage");
  }
}



