export class ColumnMappingViewModel {
/// <summary>
///  結構描述
/// <summary>
public schemaname: string;
/// <summary>
///  資料表
/// <summary>
public tablename: string;
/// <summary>
///  欄位名稱
/// <summary>
public columnname: string;
/// <summary>
///  欄位群組主鍵
/// <summary>
public column_group_key: string;
/// <summary>
///  對應欄位
/// <summary>
public mapping_column: string;
/// <summary>
///  對應欄位說明
/// <summary>
public mapping_column_desc: string;
/// <summary>
///  鍵檔人員
/// <summary>
public creator: string;
/// <summary>
///  建立時間
/// <summary>
public create_time: Date;
/// <summary>
///  異動人員
/// <summary>
public modifier: string;
/// <summary>
///  最後更新時間
/// <summary>
public last_update_time: Date;

}



